html {
  --color-black: #0E111B;
  --color-white: #FFF;
  --color-white-50: rgba(255,255,255,.5);
  --color-white-30: rgba(255,255,255,.3);
  --color-white-20: rgba(255,255,255,.2);
  --color-white-10: rgba(255,255,255,.1);
  --color-white-5: rgba(255,255,255,.05);
  --color-primary: #FDC000;
  --color-hover: #F86A05;
  --color-area: #1C212E;
}
body {
  background-color: var(--color-black);
  font-family: "Roboto", sans-serif;
  line-height: 1.4;
  color: var(--color-white);
}

.container {
  max-width: 1120px;
  margin: 0 auto;
  padding: 40px 0;
}

.btn {
  height: 50px;
  padding: 0 20px;
  border: none;
  border-radius: 4px;
  outline: none;
  font-size: 14px;
  font-weight: 700;
  color: var(--color-black);
  background-color: var(--color-primary);
  cursor: pointer;
  transition: .3s;
}
.btn:hover {
  background-color: var(--color-hover);
}
.brn-primary {
  background-color: var(--color-primary);
  color: var(--color-black);
}
.btn-primary:hover {
  background-color: var(--color-hover);
  color: var(--color-white);
}

.the-loader {
  width: 30px;
  height: 30px;
  margin: 30px auto;
  border: 4px solid var(--color-primary);
  border-top-color: transparent;
  border-radius: 50%;
  animation: loader 1s infinite linear;
}
.the-loader.hide {
  display: none;
}
@keyframes loader {
  0% {transform: rotate(0deg)}
  100% {transform: rotate(360deg)}
}

.skeleton {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  background-color: var(--color-area);
}
.skeleton::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(270deg,
    rgba(255,255,255,0),
    rgba(255,255,255,.1),
    rgba(255,255,255,0)
  );
  transform: translateX(-100%);
  animation: skeleton-loader 2s infinite;
}
@keyframes skeleton-loader {
  0% {transform: translateX(-100%);}
  100% {transform: translateX(100%);}
}

.headline {
  margin-bottom: 40px;
}
.headline h1 {
  font-family: "Oswald", sans-serif;
  font-size: 80px;
  line-height: 1;
  margin-bottom: 40px;
}
.headline h1 span{
  color: var(--color-primary);
}
.headline p {
  color: var(--color-white-30);
}

.search {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
}
.search input {
  flex-grow: 1;
  height: 50px;
  padding: 0 20px;
  border-radius: 4px;
  border: none;
  outline: none;
  font-size: 14px;
  color: var(--color-white);
  background-color: var(--color-area);
}
.search input::placeholder {
  color: var(--color-white-30);
}
.search .btn {
  flex-grow: 1;
  max-width: 150px;
}

.movie-list {
  padding: 20px;
  border-radius: 4px;
  background-color: var(--color-area);
}
.movie-list .message {
  color: var(--color-primary);
  font-size: 20px;
  text-align: center;
}
.movie-list .movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.movies .movie {
  --width: 200px;
  width: var(--width);
  height: calc(var(--width) * 3 / 2);
  border-radius: 4px;
  background-color: var(--color-black);
  background-size: cover;
  overflow: hidden;
  position: relative;
}
.movies .movie:hover::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 6px solid var(--color-primary);
}
.movies .movie .info {
  width: 100%;
  padding: 14px;
  box-sizing: border-box;
  font-size: 14px;
  text-align: center;
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: rgba(14,17,27,.3);
  backdrop-filter: blur(10px);
}
.movies .movie .info .year {
  color: var(--color-primary);
}
.movies .movie .info .title {
  color: var(--color-white);
}

.view-more {
  width: 100%;
  max-width: 300px;
  margin: 20px auto;
  display: block;
}
.view-more.hide {
  display: none;
}

.the-movie {
  color: var(--color-white-50);
  display: flex;
  gap: 70px;
}
.the-movie .poster {
  flex-shrink: 0;
  --width: 500px;
  width: var(--width);
  height: calc(var(--width) * 3 / 2);
  border-radius: 10px;
  background-size: cover;
  background-color: var(--color-area);
}
.the-movie .specs {
  flex-grow: 1;
}
.the-movie .title {
  font-size: 70px;
  font-family: "Oswald", sans-serif;
  line-height: 1;
  color: var(--color-white);
  margin-bottom: 30px;
}
.the-movie .title.skeleton {
  height: 70px;
}
.the-movie .labels {
  color: var(--color-primary);
  margin-bottom: 20px;
}
.the-movie .labels.skeleton {
  height: 30px;
}
.the-movie .plot.skeleton {
  width: 80%;
  height: 400px;
}
.the-movie h3 {
  font-size: 20px;
  font-family: "Oswald", sans-serif;
  color: var(--color-white);
  margin: 24px 0 6px;
}